/** @format */

.ant-input-group-addon {
  background: transparent;
  border: none;
}
.ant-btn[disabled] {
  background: #E9E9E9;
  border-radius: 0px;
  border: none;
  color: #C7C7C7;
  cursor: not-allowed;
  &:hover {
    background: #E9E9E9;
    color: #C7C7C7;
  }
}

.ant-modal-header {
  padding: 16px 0px;
  margin: 0px 24px;
  border-bottom: 1px solid #000;
}

.ant-modal-footer {
  padding: 24px;
  padding-top: 4px;
}

.modal-one-btn {
  width: 100%;
  height: 48px;
  font-size: 16px;
}

.ant-modal-close-x {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 24px;
}

@primary-color: #000;@link-color: #15DAD1;@text-color: #333;@text-color-secondary: #666;@heading-color: #000;@border-radius-base: 0px;@modal-content-bg: #fff;@modal-header-bg: #fff;@modal-footer-border-color-split: #fff;@modal-header-border-color-split: #fff;@modal-close-color: #333333;@component-background: #fff;@border-color-base: #000000;@btn-default-borde: #000000;@background-color-base: #fff;@body-background: #F5F7FB;@input-back: rgba(255, 255, 255, 0.8);@form-back: #F3F3F3;